import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';

function App() {
  const [input, setinput] = useState<string>('');
  const [data, setdata] = useState<string[]>([]);
  const [editItem, seteditItem] = useState<string>('');
  const onChange = (e: any) => {
    setinput(e.target.value);
  }
  const onClick = () => {
    let indexof = data.indexOf(input);
    if (input.length <= 3) {
      alert('بیشتر از 3 حرف وارد کنید');
      return
    }
    else if (indexof >= 0) {
      alert('این ایتم تکراری است');
      return
    }
    setdata([...data, input]);
    setTimeout(() => {
      alert('اطلاعات شما ثبت شد')
    }, 300);
    setinput('');
  }
  const handelDelete = (index: string) => {
    let delet = data.filter(item => item != index);
    setdata([...delet]);
  }
  const handelEdit = (item: string) => {
    setinput(item);
    seteditItem(item)
  }
  const edit = () => {
    debugger
    let search = data.indexOf(editItem);
    data[search] = input;
    setdata([...data]);
    setinput('');
    setTimeout(() => {
      alert(`ایتم ${editItem} به ${input} تغیر کرد`)
    }, 300);
  }
  return (
    <div className="App">
      <div className='flex flex-col justify-center mt-20'>
        <div className='md:w-3/6 w-full p-4 flex gap-4 m-auto mt-10'>
          <input value={input} onChange={onChange} className='bg-blue-400 text-2xl w-full rounded' type="text" />
          <div className='flex gap-4'>
            <button onClick={onClick} className='bg-blue-400 text-white text-xl p-4 rounded' >Add</button>
            <button onClick={edit} className='bg-blue-400 text-white text-xl p-4 rounded' >Edit</button>
          </div>
        </div>
        <div className='w-full md:w-3/6 m-auto space-y-6 p-4 mt-6'>
          {data.map(item => {
            return (
              <div className='flex items-center justify-between'>
                <h1 className='text-2xl'>{item}</h1>
                <div className='flex gap-4'>
                  <button onClick={() => handelDelete(item)} className='p-4 rounded bg-blue-400 text-white text-xl'>Delete</button>
                  <button onClick={() => handelEdit(item)} className='p-4 rounded bg-blue-400 text-white text-xl'>Edit</button>
                </div>
              </div>
            )
          })}
        </div>
      </div>
    </div>
  );
}

export default App;
